import React , {Component} from 'react';
import {Link} from 'react-router-dom';
class Brand extends Component{
    state={
        company:['./image/images/standard.jpg','./image/krishna.jpg','./image/narayana.jpg','./image/ramesh.jpg']
    }
    render(){
        const {company} = this.state;
        const brands = ['/standard/','/krishna/','/narayana/','/ramesh/'];
        return(
            <div className="mt-4 container">
            <div className="row">
            
            {company.map((brand,i)=>(
                <div className="col">
                    <div className="card card-body" style={{cursor:'pointer'}} key={i}>
                    
                        <Link to={brands[i]}>
                        <img src={brand} title={brand} key={i} alt={'logo'}/>
                        </Link>
                        </div>
                        </div>
                ))}
                
            </div>
                
            
               
            </div>
        );
    }
}
export default Brand;