import React from 'react';

const Item = ({
    title,
    photo,
    price,
    index
})=>{
    return(
        <div className="card card-body" style={{cursor:'pointer'}} key={index}>
            <h5>{title}</h5>
            <img src={photo} title={title} alt={'crackers'}/>
         </div>
    );
}
export default Item;