import React,{Component}  from 'react';
import Map from './Maps'
export class Contactus extends Component {
  render() {
    return (
        <div  className="container mt-4 mb-3" style={{display:'grid',gridTemplateColumns:'1fr 1fr',minHeight:'400px',gridGap:'35px'}}>
        <div>
        <h3>Contactus</h3>
        <a href="https://www.google.com/maps/place/VK+Rd,+Coimbatore,+Tamil+Nadu+641004/@11.0281856,77.0014896,20z/data=!4m5!3m4!1s0x3ba8582ff6a4de77:0x3fb2c67a20accdea!8m2!3d11.0263404!4d77.0006843" target="_blank"><img src="./image/map.jpg" alt="map" width="600px" height="470px"/></a>
        </div>
        <div className="p-2">
        <h5>Follow Us On</h5>
        <div className="bg-light p-4" style={{borderRadius:'15px'}}>
        <div>
        <a href="https://www.facebook.com/NPKCrackers-590448031400443/"><i class="fab fa-facebook-square fa-3x mr-4"></i></a> 
        <i class="fab fa-whatsapp-square fa-3x text-success"></i>
        <a href="https://mail.google.com/mail/u/0/#inbox?compose=DmwnWtVbFlWNBHwtTHXSSZcnjDslfhnVdJCLcCsTngjzNrPGlbKQLTKVhcHnXxgtNbFjtDWxNMFg" target="_blank"><i className="far fa-envelope fa-3x text-danger ml-4"></i></a>
        </div>
            <form className="mt-3 py-2">
        <div class="form-group">
            <label for="exampleInputEmail1">Name</label>
            <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Name..."/>
        </div>
        <div class="form-group">
            <label for="exampleInputPassword1">Email</label>
            <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Enter Password"/>
        </div>
        <div class="form-group">
            <label for="exampleFormControlTextarea1">Message</label>
            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
        </div>
        <button type="submit" class="btn btn-primary">Submit</button>
        </form>
        </div>
        </div>
        </div>
    );
  }
}
 
