import React ,{Component} from 'react';
import Item from './Item';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
// import 'owl.carousel/src/js/owl.carousel';
import 'owl.carousel/src/js/jquer';
class BestSelling extends Component{
    state={
        bestSelling: [
            {
                title: 'Red Bijili',
                type:'Bijili Crackers',
                photo: './image/bijili.jpg',
                price: 200
            },
            {
                title: 'Atom Bomb',
                type:'Gift Box',
                photo: './image/atom.jpg',
                price: 200
            },
            {
                title: 'Ground Chakara Deluxe',
                type:'Gift Box',
                photo: './image/standard/gc1.png',
                price: 200
            },
            {
                title: '1000 Wala',
                type:'Gift Box',
                photo: './image/standard/w2.jpg',
                price: 200
            },
            {
                title: 'Smoke Fog',
                type:'Gift Box',
                photo: './image/standard/f3.jpg',
                price: 200
            },
            {
                title: 'Flower Pot Gaint',
                type:'Gift Box',
                photo: './image/standard/fp1.png',
                price: 200
            },
            {
                title: '100 Shots',
                type:'Gift Box',
                photo: './image/krishna/f2.jpg',
                price: 200
            },
            {
                title: '60 Shots',
                type:'Gift Box',
                photo: './image/krishna/f1.jpg',
                price: 200
            },
            {
                title: 'Glory Gift Box',
                type:'Gift Box',
                photo: './image/krishna/g1.jpg',
                price: 200
            },
            {
                title: 'Classic Gift Box',
                type:'Gift Box',
                photo: './image/krishna/g2.jpg',
                price: 200
            },
            
        ]
    }
    render(){
        const {bestSelling} = this.state;
        const options = {
            center: true,
            autoplay: true,
            autoplayTimeout:1500,
            autoplayHoverPause:true
        }
        return(
            <div className="container">
                <div className="mt-4 mb-4 bg-light p-4" style={{borderRadius:'15px'}}>
                <div className="row">
                <div style={{maxWidth: '600px'}} className="col p-4">
                <h3 className="text-danger text-center mt-4 mb-4">Our Best Selling Products</h3>
                        <OwlCarousel className="owl-carousel owl-theme loop"
                            loop
                            margin={10}
                            {...options}
                            nav>
                            {bestSelling.map((item,i)=>(
                        <div className="item" key={i}>
                            <Item title={item.title} 
                                photo={item.photo} 
                                key={i} 
                                price={item.price}/>
                        </div>
                        ))}
                        </OwlCarousel>
                </div>
                <div className="text-center col" style={{borderLeft: '1px dashed red',padding:'18px'}}>
                    <h3 className="text-dark mt-4 mb-4">Download Broucher</h3>
                    <a href="/image/logo/pricelist2k18.pdf" download><i className="fas fa-download fa-2x mt-4 text-dark"></i></a>
                </div>
                </div>
                </div> 
                </div>  
        )
    }
}
export default BestSelling;