import React from 'react';

const SafetyTips = ()=>{
    return(
        <div style={{backgroundImage:'url(./image/krishna/nav.jpg)',backgroundSize: 'cover',backgroundRepeat:'no-repeat',padding:'35px'}} >
        <div className="container text-light ">
            <h3 className="text-success mb-3">DO'S</h3>
            <ul className="logo">
            <li>Display fireworks as per the warnings and instructions mentioned on the pack.</li>
            <li>Use fireworks only in outdoor.</li>
            <li>Buy fireworks directly from Manufacturer or from authorized dealer only.</li>
            <li>Always follow the Safety tips marked on the fireworks.</li>
            <li>Use an agarbatti to ignite the fireworks.</li>
            <li>Store fireworks in a cool &amp; dry place.</li>
            <li>Always have adult supervision</li>
            <li>Always wear eye protection when lightening fireworks</li>
            <li>Light one fireworks at a time</li>
            <li>Keep a bucket of water or a garden hose handy in case of fire or other mishap</li>
            </ul>
            <h3 className="text-danger mb-3">Don'ts</h3>
            <ul className="logo">
            <li>Never make your own fireworks</li>
            <li>Never try to re-light or pick up fireworks that have not ignited fully.</li>
            <li>Never carry fireworks in your packets</li>
            <li>Never shoot fireworks in a metal or glass containers</li>
            <li>Never point or throw fireworks at another person</li>
            <li>Do not wear loose clothing while using fireworks.</li>
            <li>After fireworks display never pick up fireworks that may be left over, they may still active.</li>
            </ul>
        </div>
            
        </div>
    );
}
export default SafetyTips;