import React, { Component } from 'react';
import {BrowserRouter as Router , Route,Switch} from 'react-router-dom';
import './App.css';
import 'react-owl-carousel2/src/owl.carousel'
import Header from './components/Header';
import Carosel from './components/Carosel';
import Standard from './components/Standard';
import Krishna from './components/Krishna';
import Ramesh from './components/Ramesh';
import SafetyTips from './components/SafetyTips';
import Footer from './components/Footer';
import Contactus from './components/Contactus';
import NotFound from './components/NotFound';
class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
        <Header brand={'NPK Crackers'}/>
        <Switch>
          <Route exact path="/" component={Carosel}/>
          <Route exact path="/standard" component={Standard}/>
          <Route exact path="/krishna" component={Krishna}/>
          <Route exact path="/ramesh" component={Ramesh}/>
          <Route exact path="/safety" component={SafetyTips}/>
          <Route exact path="/contactus" component={Contactus}/>
          <Route  component={NotFound}/>
        </Switch>
        <Footer />
        </div>
      </Router>
      
     
    );
  }
}

export default App;
