import React,{Component} from 'react';
import {Link} from 'react-router-dom';
class Header extends Component{
    state={
        home:false,
        contactus:false,
        safety:false
    }
    navLine = (line)=>{
        switch(line){
            case 'home':
            this.setState({home:true,contactus:false,safety:false});
            console.log(this.state);
            break
            case 'contactus':
            this.setState({home:false,contactus:true,safety:false});
            console.log(this.state);
            break
            case 'safety':
            this.setState({home:false,contactus:false,safety:true});
            console.log(this.state);
            break
        }
    }
    render(){
        const {home , contactus, safety} =this.state;
        return(
            <nav className="navbar navbar-expand-sm navbar-dark bg-dark">
            <div className="container" style={{borderRadius:'15px'}}>
            <div className="navbar-brand">
            <img src="/image/logo/logo.png" alt="logo" width="200" height="132"/>
            </div>    
            <Link to="/">
                    <span className="brands font-weight-bold">(நடராஜ் பட்டாசு கடை)</span>
                    </Link>
                <ul className="nav navbar-nav navbar-right ml-5 text-white" style={{fontSize: '25px'}}>
                
                    <li className="nav-item active">
                    <Link to="/">
                    <a className="nav-link " onClick={this.navLine.bind(this,'home')}>
                        <i className="fas fas-home "></i>Home</a>{(home ? <p style={{borderBottom: '2px solid red',color:'#fff'}}></p>: null)}
                    </Link>
                    </li>
                    <li className="nav-item">
                    <Link to="/contactus">
                    <a className="nav-link " onClick={this.navLine.bind(this,'contactus')}>
                        <i className="fas fas-plus"></i>Contact Us</a>{(contactus ? <p style={{borderBottom: '2px solid red',color:'#fff'}}></p>: null)}
                    </Link>
                    </li>
                    <li className="nav-item">
                    <Link to="/safety">
                    <a className="nav-link " onClick={this.navLine.bind(this,'safety')}>
                        <i className="fas fas-plus"></i>Safety Tips</a>{(safety ? <p style={{borderBottom: '2px solid red'}}></p>: null)}
                    </Link>
                    </li>
                </ul>
            </div>
        </nav>
    //     <nav className="navbar navbar-inverse navbar-fixed-top" role="navigation">
    //     <div className="container">
    //         <div className="navbar-header">
    //             <button type="button" class="navbar-toggle" data-toggle="collapse"
    //                     data-target="#bs-example-navbar-collapse-1">
    
    //                 <span class="sr-only">Toggle navigation</span>
    //                 <span class="icon-bar"></span>
    //                 <span class="icon-bar"></span>
    //                 <span class="icon-bar"></span>
    
    //             </button>
    //             <a className="navbar-brand">
    //             <img src="/image/logo/logo.png" alt="logo" width="200" height="132"/>
    //             </a>
    //         </div>
    
    //         <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
    //             <ul className="nav navbar-nav navbar-right ml-5 text-white" style={{fontSize: '25px'}}>
    //                 <li className="nav-item active">
    //                 <Link to="/">
    //                 <a className="nav-link " onClick={this.navLine.bind(this,'home')}>
    //                     <i className="fas fas-home "></i>Home</a>{(home ? <p style={{borderBottom: '2px solid red',color:'#fff'}}></p>: null)}
    //                 </Link>
    //                 </li>
    //                 <li className="nav-item">
    //                 <Link to="/contactus">
    //                 <a className="nav-link " onClick={this.navLine.bind(this,'contactus')}>
    //                     <i className="fas fas-plus"></i>Contact Us</a>{(contactus ? <p style={{borderBottom: '2px solid red',color:'#fff'}}></p>: null)}
    //                 </Link>
    //                 </li>
    //                 <li className="nav-item">
    //                 <Link to="/safety">
    //                 <a className="nav-link " onClick={this.navLine.bind(this,'safety')}>
    //                     <i className="fas fas-plus"></i>Safety Tips</a>{(safety ? <p style={{borderBottom: '2px solid red'}}></p>: null)}
    //                 </Link>
    //                 </li>
    //             </ul>
    //         </div>
    //     </div>
    // </nav>
        );
    }
}


export default Header;
